import { request, requestDownload } from 'utils';

const prefix = '/api/education';

export const getSubjectByFacultyAndSession = ({
  facultyId,
  registerSessionId,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: '/ClassCredit/SubjectByFacultyAndSession',
    method: 'GET',
    params: {facultyId,registerSessionId,page,pageSize,orderBy,isPagingEnabled},
    data: {}
  });

  
export const getClassCredit = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  yearEduId,
  semesterId,
  studentRegister,
  studentNotRegister,
  studentReLearn,
  studentDegreeTwo,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: '/ClassCredit/ExportClassCredit',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      yearEduId,
      semesterId,
      studentRegister,
      studentNotRegister,
      studentReLearn,
      studentDegreeTwo,
      page,
      pageSize,
      orderBy,
      isPagingEnabled
    },
    data: {}
  });


  
export const searchSubject = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  yearEduId,
  semesterId,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: '/ClassCredit/SearchSubject',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      yearEduId,
      semesterId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled
    },
    data: {}
  });


export const createClassCredit = data =>
  request({
    prefix,
    url: '/ClassCredit/CreateOrUpdateClassCredit',
    method: 'POST',
    params: {},
    data
  });

    
export const exportClassCreditExcel = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  yearEduId,
  semesterId,
  studentRegister,
  studentNotRegister,
  studentReLearn,
  studentDegreeTwo
} = {}) =>
  requestDownload({
    prefix,
    url: '/ClassCredit/ExportClassCreditExcel',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      yearEduId,
      semesterId,
      studentRegister,
      studentNotRegister,
      studentReLearn,
      studentDegreeTwo
    },
    data: {},
    defaultFileName: 'ExportClassCreditExcel.xlsx',
  });
  


